import React, {useCallback, useEffect, useState} from 'react';
import {makeStyles} from '@material-ui/core/styles';
import CssBaseline from '@material-ui/core/CssBaseline';
import Toolbar from '@material-ui/core/Toolbar';
import SideMenu from '../components/SideMenu';
import Typography from '@material-ui/core/Typography';
import UserAvatar from '../components/UserAvatar';
import AppBar from '@material-ui/core/AppBar';
import Container from '@material-ui/core/Container';
import Paper from '@material-ui/core/Paper';
import TableContainer from '@material-ui/core/TableContainer';
import Table from '@material-ui/core/Table';
import TableRow from '@material-ui/core/TableRow';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import IconButton from '@material-ui/core/IconButton';
import AddIcon from '@material-ui/icons/Add';
import Tooltip from '@material-ui/core/Tooltip';
import Alert from '@material-ui/lab/Alert';
import Snackbar from '@material-ui/core/Snackbar';
import DialogTitle from '@material-ui/core/DialogTitle';
import DialogContent from '@material-ui/core/DialogContent';
import TextField from '@material-ui/core/TextField';
import FormControl from '@material-ui/core/FormControl';
import InputLabel from '@material-ui/core/InputLabel';
import NativeSelect from '@material-ui/core/NativeSelect';
import DialogActions from '@material-ui/core/DialogActions';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import Grid from '@material-ui/core/Grid';
import MonthOptions from "../components/MonthOptions";
import axios from "axios";
import userStore from "../stores/UserStore";
import {TableBody} from "@material-ui/core";
import EditIcon from "@material-ui/icons/Edit";
import DeleteIcon from "@material-ui/icons/Delete";

const useStyles = makeStyles(theme => ({
    root: {
        flexGrow: 1,
        '& .MuiTypography-h5': {
            fontWeight: 600
        },
        '& .MuiTypography-h6': {
            fontWeight: 600
        }
    },
    title: {
        flexGrow: 1
    },
    container: {
        marginTop: theme.spacing(2),
        marginBottom: theme.spacing(2),
    },
    tableToolbar: {
        '& .MuiFormControl-root': {
            marginRight: theme.spacing(2),
            marginLeft: theme.spacing(1),
            width: 100
        },
        marginBottom: theme.spacing(1)
    },
    tableContainer: {
        marginBottom: theme.spacing(3)
    },
    table: {
        '& .MuiTableCell-root': {
            whiteSpace: 'nowrap',
            '& .MuiTextField-root': {
                width: 100
            }
        },
        '& .MuiTableHead-root': {
            '& .MuiTableRow-root': {
                '& .MuiTableCell-root': {
                    fontWeight: 600
                }
            }
        }
    },
    dialogTitle: {
        '& .MuiTypography-h6': {
            fontWeight: 600
        }
    }
}));

const classificationMap = { '10': '', '11': '' }
const breakdownMap = { '10': '', '11': '', '12': 'IC', '13': '' }
const billingAddressMap = { '1': '', '2': '' }

export default function Expense() {
    const classes = useStyles();
    const [severity, setSeverity] = useState('error');
    const [message, setMessage] = useState('');
    const [snackbarOpen, setSnackbarOpen] = useState(false);
    const [addTransportOpen, setAddTransportOpen] = useState(false);
    const [selectedYear, setSelectedYear] = useState(new Date().getFullYear());
    const [selectedMonth, setSelectedMonth] = useState(new Date().getMonth() + 1);
    const [year, setYear] = useState(selectedYear);
    const [month, setMonth] = useState(selectedMonth);
    const [day, setDay] = useState(new Date().getDate());
    const [customer, setCustomer] = useState('');
    const [classification, setClassification] = useState(10);
    const [breakdown, setBreakdown] = useState(10);
    const [billingAddress, setBillingAddress] = useState(1);
    const [expenses, setExpenses] = useState(0);
    const [transportation, setTransportation] = useState('');
    const [departure, setDeparture] = useState('');
    const [arrival, setArrival] = useState('');
    const [transportRows, setTransportRows] = useState([]);
    const [tranExId, setTranExId] = useState(0);

    /**
     * 
     * @type {function(): void}
     */
    const loadTransportationExpenses = useCallback(() => {
        axios.get(`${process.env.REACT_APP_REST_URI}/expenses/${selectedYear}/${selectedMonth}`, {
            params: {
                token: userStore.getAccessToken()
            }
        }).then((response) => {
            const records = response.data['records'];
            const rows = records.map(record => {
                return {
                    id: record['tran_ex_id'],
                    day: record['day'],
                    customer: record['customer'],
                    classification: record['classification'],
                    breakdown: record['breakdown'],
                    billing_address: record['billing_address'],
                    expenses: record['expenses'],
                    transportation: record['transportation'],
                    departure: record['departure'],
                    arrival: record['arrival']
                };
            });
            setTransportRows(rows);
        }).catch((error) => {
            setSeverity('error');
            setMessage(error['message']);
            setSnackbarOpen(true);
        });
    }, [selectedYear, selectedMonth]);

    /**
     * 
     */
    useEffect(() => {
        if (selectedYear > 0) {
            loadTransportationExpenses();
        }
    }, [selectedYear, selectedMonth, loadTransportationExpenses]);

    /**
     * 
     *
     * @returns {[]} 
     * @constructor
     */
    const DateOptions = () => {
        const options = [];
        const now = new Date(selectedYear, selectedMonth, 1);
        const work = new Date(now.getFullYear(), now.getMonth(), 1);
        while (true) {
            const date = work.getDate();
            options.push(<option key={date}>{date}</option>);
            work.setDate(date + 1);
            if (now.getMonth() !== work.getMonth()) {
                break;
            }
        }
        return options;
    };

    /**
     * 
     *
     * @param event 
     * @param reason 
     */
    const handleCloseSnackbar = (event, reason) => {
        if ('clickaway' === reason) {
            return;
        }
        setSnackbarOpen(false);
    };

    /**
     * 
     */
    const handleClickAddTransport = (row) => {
        if (null !== row) {
            setYear(selectedYear);
            setMonth(selectedMonth);
            setDay(row['day']);
            setCustomer(row['customer']);
            setClassification(row['classification']);
            setBreakdown(row['breakdown']);
            setBillingAddress(row['billing_address']);
            setExpenses(row['expenses']);
            setTransportation(row['transportation']);
            setDeparture(row['departure']);
            setArrival(row['arrival']);
            setTranExId(row['id']);
        }
        else {
            const now = new Date();
            setYear(selectedYear);
            setMonth(selectedMonth);
            setDay(now.getDate());
            setCustomer('');
            setClassification(10);
            setBreakdown(10);
            setBillingAddress(1);
            setExpenses(0);
            setTransportation('');
            setDeparture('');
            setArrival('');
            setTranExId(0);
        }
        setAddTransportOpen(true);
    };

    /**
     * 
     *
     * @param event 
     */
    const handleChangeExpenses = (event) => {
        if (/^([1-9]\d*|0)$/.test(event.target.value)) {
            setExpenses(Number(event.target.value));
        }
        else {
            event.target.value = '';
        }
    };

    /**
     * 
     */
    const handleClickDialogAddTransport = () => {
        if (!customer) {
            setSeverity('error');
            setMessage('');
            setSnackbarOpen(true);
            return;
        }
        if (!expenses) {
            setSeverity('error');
            setMessage('');
            setSnackbarOpen(true);
            return;
        }
        if (!transportation) {
            setSeverity('error');
            setMessage('');
            setSnackbarOpen(true);
            return;
        }
        if (!departure) {
            setSeverity('error');
            setMessage('');
            setSnackbarOpen(true);
            return;
        }
        if (!arrival) {
            setSeverity('error');
            setMessage('');
            setSnackbarOpen(true);
            return;
        }

        if (0 !== tranExId) {
            console.log(`year:${selectedYear},month:${selectedMonth},day${day}`);
            axios.put(`${process.env.REACT_APP_REST_URI}/expenses/${tranExId}`, {
                year: selectedYear,
                month: selectedMonth,
                day: day,
                customer: customer,
                classification: classification,
                breakdown: breakdown,
                billing_address: billingAddress,
                expenses: expenses,
                transportation: transportation,
                departure: departure,
                arrival: arrival
            }, {
                params: {
                    token: userStore.getAccessToken()
                }
            }).then((response) => {
                const record = response.data['record'];
                const row = transportRows.find(r => r.id === tranExId);
                row.id = record['tran_ex_id'];
                row.day = record['day'];
                row.customer = record['customer'];
                row.classification = record['classification'];
                row.breakdown = record['breakdown'];
                row.billing_address = record['billing_address'];
                row.expenses = record['expenses'];
                row.transportation = record['transportation'];
                row.departure = record['departure'];
                row.arrival = record['arrival'];
                setTransportRows(transportRows.slice());
                setAddTransportOpen(false);
                setSeverity('success');
                setMessage('');
                setSnackbarOpen(true);
            }).catch((error) => {
                setSeverity('error');
                setMessage(error['message']);
                setSnackbarOpen(true);
            });
        }
        else {
            axios.post(`${process.env.REACT_APP_REST_URI}/expenses/${year}/${month}/${day}`, {
                customer: customer,
                classification: classification,
                breakdown: breakdown,
                billing_address: billingAddress,
                expenses: expenses,
                transportation: transportation,
                departure: departure,
                arrival: arrival
            }, {
                params: {
                    token: userStore.getAccessToken()
                }
            }).then((response) => {
                const record = response.data['record'];
                transportRows.push({
                    id: record['tran_ex_id'],
                    day: record['day'],
                    customer: record['customer'],
                    classification: record['classification'],
                    breakdown: record['breakdown'],
                    billing_address: record['billing_address'],
                    expenses: record['expenses'],
                    transportation: record['transportation'],
                    departure: record['departure'],
                    arrival: record['arrival']
                });
                setTransportRows(transportRows.slice());
                setAddTransportOpen(false);
                setSeverity('success');
                setMessage('');
                setSnackbarOpen(true);
            }).catch((error) => {
                setSeverity('error');
                setMessage(error['message']);
                setSnackbarOpen(true);
            });
        }
    };

    /**
     * 
     *
     * @param row 
     */
    const handleClickTransportDelete = (row) => {
        axios.delete(`${process.env.REACT_APP_REST_URI}/expenses/${row.id}`, {
            params: {
                token: userStore.getAccessToken()
            }
        }).then(() => {
            delete transportRows[transportRows.indexOf(row)];
            setTransportRows(transportRows.slice());
            setSeverity('success');
            setMessage('');
            setSnackbarOpen(true);
        }).catch((error) => {
            setSeverity('error');
            setMessage(error['message']);
            setSnackbarOpen(true);
        });
    };

    return (
        <form className={classes.root}>
            <CssBaseline/>

            <AppBar position='sticky'>
                <Toolbar>
                    <SideMenu/>
                    <Typography variant='h6' className={classes.title}>
                        
                    </Typography>
                    <UserAvatar/>
                </Toolbar>
            </AppBar>

            <Snackbar open={snackbarOpen} autoHideDuration={6000} onClose={handleCloseSnackbar}>
                <Alert severity={severity} variant="filled" onClose={handleCloseSnackbar}>{message}</Alert>
            </Snackbar>

            <Dialog open={addTransportOpen} onClose={() => setAddTransportOpen(false)}>
                <DialogTitle className={classes.dialogTitle}></DialogTitle>
                <DialogContent>
                    <Grid container={true} spacing={2}>
                        <Grid item={true} xs={4}>
                            <TextField label="" fullWidth={true} required={true} margin="dense"
                                       defaultValue={year} disabled={true} />
                        </Grid>
                        <Grid item={true} xs={4}>
                            <TextField label="" fullWidth={true} required={true} margin="dense"
                                       defaultValue={month} disabled={true} />
                        </Grid>
                        <Grid item={true} xs={4}>
                            <FormControl required={true} fullWidth={true} margin="dense">
                                <InputLabel htmlFor="select-date"></InputLabel>
                                <NativeSelect value={day} inputProps={{id: 'select-date'}}
                                              onChange={(event) => setDay(Number(event.target.value))}>
                                    <DateOptions />
                                </NativeSelect>
                            </FormControl>
                        </Grid>
                        <Grid item={true} xs={12}>
                            <TextField autoFocus={true} label="" fullWidth={true} required={true} margin="dense"
                                       defaultValue={customer}
                                       onChange={(event) => setCustomer(event.target.value)} />
                        </Grid>
                        <Grid item={true} xs={4}>
                            <FormControl required={true} fullWidth={true} margin="dense">
                                <InputLabel htmlFor="select-classification"></InputLabel>
                                <NativeSelect value={classification} inputProps={{id: 'select-classification'}}
                                              onChange={(event) => setClassification(Number(event.target.value))}>
                                    <option value={10}></option>
                                    <option value={11}></option>
                                </NativeSelect>
                            </FormControl>
                        </Grid>
                        <Grid item={true} xs={4}>
                            <FormControl required={true} fullWidth={true} margin="dense">
                                <InputLabel htmlFor="select-breakdown"></InputLabel>
                                <NativeSelect value={breakdown} inputProps={{id: 'select-breakdown'}}
                                              onChange={(event) => setBreakdown(Number(event.target.value))}>
                                    <option value={10}></option>
                                    <option value={11}></option>
                                    <option value={12}>IC</option>
                                    <option value={13}></option>
                                </NativeSelect>
                            </FormControl>
                        </Grid>
                        <Grid item={true} xs={4}>
                            <FormControl required={true} fullWidth={true} margin="dense">
                                <InputLabel htmlFor="select-billing-address"></InputLabel>
                                <NativeSelect value={billingAddress} inputProps={{id: 'select-billing-address'}}
                                              onChange={(event) => setBillingAddress(Number(event.target.value))}>
                                    <option value={1}></option>
                                    <option value={2}></option>
                                </NativeSelect>
                            </FormControl>
                        </Grid>
                        <Grid item={true} xs={4}>
                            <TextField label="" fullWidth={true} required={true} margin="dense"
                                       defaultValue={expenses}
                                       onChange={(event) => handleChangeExpenses(event)} />
                        </Grid>
                        <Grid item={true} xs={8}>
                            <TextField label="" fullWidth={true} required={true} margin="dense"
                                       defaultValue={transportation}
                                       onChange={(event) => setTransportation(event.target.value)} />
                        </Grid>
                        <Grid item={true} xs={4}>
                            <TextField label="" fullWidth={true} required={true} margin="dense"
                                       defaultValue={departure}
                                       onChange={(event) => setDeparture(event.target.value)} />
                        </Grid>
                        <Grid item={true} xs={4}>
                            <TextField label="" fullWidth={true} required={true} margin="dense"
                                       defaultValue={arrival}
                                       onChange={(event) => setArrival(event.target.value)} />
                        </Grid>
                    </Grid>
                </DialogContent>
                <DialogActions>
                    <Button color="primary" onClick={() => setAddTransportOpen(false)}>
                        
                    </Button>
                    <Button color="primary" onClick={handleClickDialogAddTransport}>
                        
                    </Button>
                </DialogActions>
            </Dialog>

            <Container maxWidth='lg' className={classes.container}>
                <Toolbar className={classes.tableToolbar}>
                    <Typography variant="h5" className={classes.title}>
                        
                    </Typography>
                    <TextField required={true} label="" margin="normal" defaultValue={selectedYear}
                               onChange={(event) => setSelectedYear(Number(event.target.value))} />
                    <FormControl required={true} margin="normal">
                        <InputLabel htmlFor="select-month"></InputLabel>
                        <NativeSelect value={selectedMonth} inputProps={{id: 'select-month'}}
                                      onChange={(event) => setSelectedMonth(event.target.value)}>
                            <MonthOptions />
                        </NativeSelect>
                    </FormControl>
                </Toolbar>
                <TableContainer component={Paper} className={classes.tableContainer}>
                    <Toolbar>
                        <Typography variant="h6" className={classes.title}></Typography>
                        <Tooltip title="">
                            <IconButton onClick={() => handleClickAddTransport(null)}>
                                <AddIcon />
                            </IconButton>
                        </Tooltip>
                    </Toolbar>
                    <Table size="small" className={classes.table} aria-label="expenses">
                        <TableHead>
                            <TableRow>
                                <TableCell align="center"></TableCell>
                                <TableCell align="center"></TableCell>
                                <TableCell align="left"></TableCell>
                                <TableCell align="left"></TableCell>
                                <TableCell align="right"></TableCell>
                                <TableCell align="left"></TableCell>
                                <TableCell align="left"></TableCell>
                                <TableCell align="left"></TableCell>
                                <TableCell align="left"></TableCell>
                                <TableCell align="center"></TableCell>
                            </TableRow>
                        </TableHead>
                        <TableBody>
                            {transportRows.map(row => (
                                <TableRow key={row.id}>
                                    <TableCell align="center"><span>{row.day}</span></TableCell>
                                    <TableCell align="center"><span>{row.customer}</span></TableCell>
                                    <TableCell align="left"><span>{classificationMap[row.classification]}</span></TableCell>
                                    <TableCell align="left"><span>{breakdownMap[row.breakdown]}</span></TableCell>
                                    <TableCell align="right">{row.expenses}</TableCell>
                                    <TableCell align="left">{billingAddressMap[row.billing_address]}</TableCell>
                                    <TableCell align="left">{row.transportation}</TableCell>
                                    <TableCell align="left">{row.departure}</TableCell>
                                    <TableCell align="left">{row.arrival}</TableCell>
                                    <TableCell align="center">
                                        <IconButton aria-label="edit" size="small" onClick={() => handleClickAddTransport(row)}>
                                            <EditIcon />
                                        </IconButton>
                                        <IconButton aria-label="delete" size="small" onClick={() => handleClickTransportDelete(row)}>
                                            <DeleteIcon />
                                        </IconButton>
                                    </TableCell>
                                </TableRow>
                            ))}
                        </TableBody>
                    </Table>
                </TableContainer>
                <TableContainer component={Paper} className={classes.tableContainer}>
                    <Toolbar>
                        <Typography variant="h6" className={classes.title}></Typography>
                        <Tooltip title="">
                            <IconButton>
                                <AddIcon />
                            </IconButton>
                        </Tooltip>
                    </Toolbar>
                    <Table size="small" className={classes.table} aria-label="expenses">
                        <TableHead>
                            <TableRow>
                                <TableCell align="center"></TableCell>
                                <TableCell align="center"></TableCell>
                                <TableCell align="center"></TableCell>
                                <TableCell align="center"></TableCell>
                                <TableCell align="center"></TableCell>
                                <TableCell align="center"></TableCell>
                                <TableCell align="center"></TableCell>
                            </TableRow>
                        </TableHead>
                    </Table>
                </TableContainer>
            </Container>
        </form>
    );
}